import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';

import { Socket } from 'ng-socket-io';

import { Storage } from '@ionic/storage';
import { ToastController } from 'ionic-angular';
import { Toast } from 'ionic-angular/components/toast/toast';

interface WordObject {
  word: string;
  tooltip: string;
  translation: string[];
  occurence: number;
  mistakes: number;
}

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  private data: WordObject[]
  private currentWord: WordObject
  private correctAnswer: string
  public hexColor: string = "#00cc00"
  private getToast: Toast
  private sendToast: Toast

  constructor(public navCtrl: NavController, private socket: Socket, private storage: Storage, private toastCtrl: ToastController) {
    this.setData()
    // this.socket.connect();
    this.socket.on('dictexport', (data) => {
      this.data = data;
      this.setData();
      this.storage.set('dict1', data);
    });
  }

  checkAnswer(event) {
    const optionA = document.getElementById("buttonA");    
    const optionB = document.getElementById("buttonB");
    const optionC = document.getElementById("buttonC");
    const optionD = document.getElementById("buttonD");

    const answer = event.target.innerHTML;

    if(answer !== this.correctAnswer) {
      this.currentWord.mistakes += 1
      this.storage.set('dict1', {wordObjectArray: this.data});
      
      this.storage.get('dict1').then((val) => {
        console.log(val);
      })
    }

    const allOptions = [optionA, optionB, optionC, optionD];

    let arrayWithWrongAnswers = [];

    allOptions.forEach((option) => {      
      // nimmt den Text des ersten Kindelements (span) und gleicht in mit der Antwort ab
      // Button ist immer groÃŸgeschrieben...
      if(option.innerText  !== this.correctAnswer.toUpperCase()) {
        arrayWithWrongAnswers.push(option);
      }
    })

    arrayWithWrongAnswers.forEach((option) => {
      option.setAttribute("style", "background-color: #ff0000");
    })


    setTimeout(() => {
      optionA.setAttribute("style", "background-color:" + this.hexColor);
      optionB.setAttribute("style", "background-color:" + this.hexColor);
      optionC.setAttribute("style", "background-color:" + this.hexColor);
      optionD.setAttribute("style", "background-color:" + this.hexColor);
      this.setAnswers()
    }, 1000);

    
    // document.getElementById("button1").innerHTML = "somethings";
  }

  setQuestionAndAnswers(questionWord: string, tooltip: string, answerA: string, answerB: string, answerC: string, answerD: string) {
    document.getElementById("questionDiv").innerHTML = questionWord;
    document.getElementById("tooltipDiv").innerHTML = tooltip;
    document.getElementById("buttonA").innerHTML = answerA;
    document.getElementById("buttonB").innerHTML = answerB;
    document.getElementById("buttonC").innerHTML = answerC;
    document.getElementById("buttonD").innerHTML = answerD;
  }

  sendData() {
    this.sendToast = this.toastCtrl.create({
      message: 'Daten erfolgreich gesendet',
      duration: 1500,
      position: 'bottom'
    });

    this.storage.get('dict1').then((val) => {
      // this.socket.connect();
      this.socket.emit('ocha', val);
      console.log("lolglgologlgolgo");
    });
      
    this.sendToast.present();
  }

  // statt ngOnInit(): void
  getData() {
    this.getToast = this.toastCtrl.create({
      message: 'Daten erfolgreich geladen',
      duration: 1500,
      position: 'bottom'
    }); 

    // const socket = socketIo('192.168.178.28:3000');
    // this.socket.connect();
    this.socket.emit('ocha', 'getData')

    this.getToast.present();
  }

  setData() {
    this.storage.get('dict1').then((val) => {
      if(val === null || val.length < 1) return
      
      this.data = val["wordObjectArray"];
      console.log(this.data);
      this.setAnswers();
    })
  }

  setAnswers() {
    if(this.data.length < 4) {
      console.log("Weniger als 4 WÃ¶rter");
      return
    }
    const randomNumberInArray = Math.floor(Math.random() * (this.data.length - 1));
    const randomOption = this.data[randomNumberInArray]
    this.currentWord = randomOption

    const questionWord = randomOption.word;
    const tooltip = randomOption.tooltip;
    this.correctAnswer = randomOption.translation[0]
    
    let wrongAnswer: string[] = []

    for(let i = 0; i < 3; i++) {
      let randomNumber = Math.floor(Math.random() * (this.data.length - 1))
      
      while(randomNumberInArray === randomNumber) {
        
        randomNumber = Math.floor(Math.random() * (this.data.length - 1))
      }
      wrongAnswer.push(this.data[randomNumber].translation[0])
    }

    const positionForCorrectAnswer = Math.floor(Math.random() * 4);

    wrongAnswer.splice(positionForCorrectAnswer, 0, this.correctAnswer);

    this.setQuestionAndAnswers(questionWord, tooltip, wrongAnswer[0], wrongAnswer[1], wrongAnswer[2], wrongAnswer[3]);
  }

}



